.nav-container {
  display: flex;
  min-height: 12vh;
  padding: 0.8rem 1.2rem 0.8rem 0.8rem;
  align-items: center;
  justify-content: space-between;
  box-shadow: 2px 2px 5px rgb(0 0 0 / 20%);
  background-color: var(--light-color);
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10;
  width: 100%;
}
.logo {
  margin: 0 1rem;
}

.left-navbar,
.nav-icons {
  display: flex;
}
.navbar-link-list-item {
  margin: 0 1rem;
}
.right-navbar,.navbar-links  {
  display: flex;
  align-items: center;
}
.badge-icon {
  font-size: 1.3rem;

}
.badge-count{
  border-radius: 50%;
  width:20px;
  height: 20px;
  display: flex;
  justify-content: center;
}
.search-bar-container {
  display: flex;
  align-items: center;
  margin: 0 0.5rem;
  background-color: rgb(221, 221, 221);
  border-radius: 0.3rem;
}
.search-bar-container button {
  height: 2rem;
  padding: 0 0.5rem;
  margin: 0;
  border: none;
  background-color: transparent;
}
.search-bar {
  height: 2rem;
  background-color: transparent;
  border: none;
  outline: 0px;
}
.nav-icon-link {
  align-items: center;
  border: none;
  background-color: transparent;
}
.dropbtn {
  color: var(--dark-color);
  font-size: 1rem;
  border: none;
}
.dropdown {
  position: relative;
  display: inline-block;
}
/* Dropdown Content*/
.dropdown-content {
  display: none;
  position: absolute;
  top: 3rem;
  left: 0rem;
  background-color: var(--light-color);
  min-width: 10rem;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
/* Links inside the dropdown */
.dropdown-content a {
  color: var(--dark-color);
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}
.dropdown-content a:hover {
  background-color: var(--dropdown-bg);
}

/* Show the dropdown menu on hover */
.dropdown:hover .dropdown-content {
  display: block;
}
.dropdown:hover .dropbtn {
  color: var(--dark-pink);
}

.wishlist-button {
