.blob-top-left {
  position: fixed;
  top: -6.25rem;
  left: -120px;
}
.blob-bottom-right {
  position: fixed;
  bottom: -6.25rem;
  right: -6.25rem;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.form-heading {
