}
input:not(:-ms-input-placeholder) + label {
    transform: none;
}
input:focus + label, input:not(:placeholder-shown) + label {
    -webkit-transform: none;
            transform: none;
}

@media only screen and (max-width: 550px) {
  .authentication-details-container {
